@import url(https://fonts.googleapis.com/css2?family=Bangers&family=Open+Sans:wght@300&display=swap);

#root,html,body{margin:0;padding:0;width:100%;height:100%}.App{display:flex;flex-direction:column;align-items:center;font-family:'Bangers', cursive;background-image:linear-gradient(135deg, #43CBFF 10%, #9708CC 100%);color:white;padding:4em 1.5em}.App h1,.App h2{font-family:'Bangers', cursive;color:#2d2d2d;margin:0}.App p{font-family:'Open Sans', sans-serif;font-weight:700;margin:0;margin-top:10px}.App h1{font-size:60px;line-height:1;text-align:center}

.card-container{display:flex;flex-direction:column;background:rgba(255,255,255,0.25);backdrop-filter:blur(14px);-webkit-backdrop-filter:blur(14px);color:black;text-align:center;border-radius:10px;padding:25px;cursor:pointer;-moz-osx-font-smoothing:grayscale;-webkit-backface-visibility:hidden;backface-visibility:hidden;transform:translateZ(0);transition:transform 0.25s ease-out}.card-container:hover{transform:translateY(-8px)}.card-container h2{margin:0;margin-top:20px;letter-spacing:1px}

.card-list{display:grid;grid-template-columns:repeat(auto-fill, minmax(350px, 1fr));grid-gap:20px;width:85vw;margin:0 auto}.card-notfound{padding:3em 0}.card-notfound h2{font-size:46px;line-height:1}

.search{-webkit-appearance:none;border:none;outline:none;padding:10px;min-width:280px;font-family:'Open Sans', sans-serif;line-height:30px;margin-top:30px;margin-bottom:40px;border-radius:10px}

